import Vue from 'vue'
//import AaochatTab from './views/AaochatTab'
import { translate as t, translatePlural as n } from '@nextcloud/l10n'

// eslint-disable-next-line n/no-missing-import, import/no-unresolved
//import aaochatIcon from '@mdi/svg/svg/lightning-bolt.svg?raw'
import aaochatIcon from '../img/app.svg?raw'

Vue.prototype.t = t
Vue.prototype.n = n

// eslint-disable-next-line
__webpack_nonce__ = btoa(OC.requestToken);
// eslint-disable-next-line
__webpack_public_path__ = OC.linkTo('aaochat', 'js/');

// Init Activity tab component
let AaochatTabView = null
let AaochatTabInstance = null
let loaded = false;


window.addEventListener('DOMContentLoaded', async function () {
	setInterval(()=>{
		if(!loaded){
			loadAaochatSiteBar();
		}
	},2000);
});

var fileChangeDebouncer;

const loadAaochatSiteBar =  async function(){
	if (OCA.Files && OCA.Files.Sidebar) {

		const aaochatTab = new OCA.Files.Sidebar.Tab({
			id: 'aaochat',
			name: t('aaochat', 'Aao'),
			iconSvg: aaochatIcon,
		
			async mount(el, fileInfo, context) {
				const { default: AaochatTab } = await import(/* webpackPreload: true */ './views/AaochatTab.vue')
				AaochatTabView = AaochatTabView ?? Vue.extend(AaochatTab)
				if (AaochatTabInstance) {
					AaochatTabInstance.$destroy()
				}
				AaochatTabInstance = new AaochatTabView({
					// Better integration with vue parent component
					parent: context,
				})
				// Only mount after we have all the info we need
				await AaochatTabInstance.update(fileInfo)
				AaochatTabInstance.$mount(el)
			},
			update(fileInfo) {
				clearTimeout(fileChangeDebouncer);
				AaochatTabInstance.update(null);
				fileChangeDebouncer = setTimeout(function(){
					AaochatTabInstance.update(fileInfo)
				},300);
				
			},
			destroy() {
				AaochatTabInstance.$destroy()
				AaochatTabInstance = null
			},
		})

		const hasDuplicate = OCA.Files.Sidebar._state.tabs.findIndex(check => check.id === aaochatTab.id) > -1
		if (!hasDuplicate) {
			OCA.Files.Sidebar.registerTab(aaochatTab);
			const { default: AaochatTab } = await import(/* webpackPreload: true */ './views/AaochatTab.vue')
			AaochatTabView = AaochatTabView ?? Vue.extend(AaochatTab)
			loaded = true;
		}

	}
}